.badge {
  position: relative;
  left: 5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;

  &--grey {
    background-color: #c9d1d3;
  }

  &--lime {
    background-color: #b6e6bd;
  }

  &--purple {
    background-color: #c355f5;
  }

  &--black {
    background-color: #08001a;
  }

  &--red {
    background-color: #ff6464;
  }

  &--green {
    background-color: #42b883;
  }

  &--blue {
    background-color: #64c4ed;
  }

  &--pink {
    background-color: #ffbbcc;
  }
}

i {
  display: inline-flex;
  margin-right: 8px;
  svg {
    path {
      fill: #7c7c7c;
    }
  }
}
